/* card */
.card {
  width: 70%;
  height: 80%;
  margin: 1rem auto;
  padding: 10px;
  background: white;
  box-shadow: 2px 4px 25px rgba(0, 0, 0, .1);
  border-radius: 12px;
  overflow: hidden;
  transition: all .2s linear;
}

.movie > .jumbotron {
  height: 350px;
}
.movie {
  overflow: hidden;
}

.scroll {
  overflow-y: auto;
}
.container {
    height: 1000px;
}


.category-services {
    justify-content: flex-end;
